/* External CSS */
/* Site Styles */
html,
body {
  height: 100%;
}
body {
  min-width: initial;
}
/* Header */
.us-header {
  background-color: #f68121;
  color: #ffffff;
  width: 100%;
  position: fixed;
  z-index: 9998;
}
.top-spacing {
  padding-top: 56px;
}
.cf-hdr-blk .cf-us-logo {
  background: url(../../images/unit-selector_logo.png) no-repeat;
  display: block;
  top: -20px;
  left: 12px;
  width: 173px;
  height: 38px;
}
.header-cta {
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 18px;
}
.proj-name {
  margin-bottom: 15px;
  margin-top: 15px;
  display: inline-block;
  text-transform: capitalize;
}
.proj-name small {
  color: #ffffff;
}
.proj-type-count {
  float: left;
}
.proj-type-count h1 {
  margin: 0 0 0 15px;
  padding: 5px 5px 10px 10px;
  border-left: 1px solid #db6809;
}
.proj-type-count p {
  margin: 22px 15px 0 2px;
  font-weight: bold;
}
.wrapper {
  min-height: 100%;
  margin-bottom: -98px;
}
.component-body {
  background-color: #ffffff;
}
.no-found-container {
  padding-top: 25vh;
}
/* Left Container */
.search-left-content {
  transition: 0.3s all cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  margin-top: 10px;
  padding-right: 15px;
}
@media (min-width: 992px) {
  .search-left-content {
    min-height: 750px;
    padding-right: 0;
  }
}
.us-left-content {
  transition: 0.3s all cubic-bezier(0.4, 0, 0.2, 1);
}
.filters-wrapper .tab-main-container {
  border-right: 0;
}
.filters-wrapper .blck-wrap:last-child {
  border-bottom: 0;
}
.marker-img {
  margin: auto;
}
/* Right Container */
.us-right-content {
  padding-left: 0;
  padding-right: 0;
  transition: 0.3s all cubic-bezier(0.4, 0, 0.2, 1);
}
.us-right-content .svg-area {
  width: 100%;
  overflow: hidden;
  min-width: 1024px;
}
.us-right-content .svg-area img {
  width: 100%;
}
.col-0 {
  width: 0;
  padding: 0;
  height: 0;
}
.col-0 div,
.col-0 ul,
.col-0 li {
  margin: 0;
  padding: 0;
}
.col-0.not-visible .two .blocks {
  padding: 0;
}
@media (max-width: 991px) {
  .us-right-content.mobile.visible,
  .us-left-content.mobile.visible {
    display: block;
    height: 100%;
    width: 100%;
  }
  .us-right-content.mobile.not-visible,
  .us-left-content.mobile.not-visible {
    display: none;
  }
  .us-right-content {
    overflow: auto;
  }
  .us-right-content .svg-area,
  .us-right-content .master {
    min-width: 165vh;
  }
  .us-right-content.unit-slides {
    overflow: visible;
  }
  .us-right-content.unit-slides .svg-area {
    min-width: 0;
  }
}
@media (max-width: 767px) {
  .us-right-content.mobile,
  .us-left-content.mobile {
    margin-top: 44px;
  }
}
/* Footer */
#footer .footer-section1 {
  background-color: #ffffff;
}
.footer-push {
  height: 98px;
}
/* Step 1*/
.step1 {
  padding-right: 15px;
}
.step1 .proj-logo,
.step1 .proj-details {
  padding: 10px 0 0 10px;
}
.step1 .proj-logo h3,
.step1 .proj-details h3 {
  text-transform: uppercase;
}
.step1 .proj-logo .img,
.step1 .proj-details .img {
  max-width: 200px;
}
.step1 .prop-types {
  padding: 15px 0 10px 10px;
  border-bottom: 1px solid #e6e6e6;
}
.step1 .prop-types.Apartments .prop-icon {
  height: 32px;
  width: 40px;
  background: url('../../images/sprite-popover2.png') no-repeat;
  background-position: -50px -15px;
  background-size: 200px;
  display: inline-block;
}
.step1 .prop-types.Villas .prop-icon,
.step1 .prop-types.Bungalows .prop-icon {
  background-position: -8px -15px;
}
.step1 .prop-types.Plot .prop-icon {
  background-position: -100px -15px;
}
.step1 .prop-types .unit-types {
  display: inline-block;
  margin-left: 10px;
}
.step1 .prop-types .unit-types span {
  font-weight: bold;
}
.step1 .info-slider {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 160px;
}
.step1 .info-slider img {
  width: 100px;
}
/* Step 2 */
.header-info {
  margin-left: 60px;
}
@media (max-width: 767px) {
  .header-info {
    margin-left: 0;
  }
  .header-info .proj-name {
    font-size: 20px;
    margin-left: 35px;
    margin-bottom: 8px;
  }
  .header-info .proj-type-count {
    width: 100%;
    margin: 10px 0;
    background-color: #db6809;
  }
  .header-info .proj-type-count h1 {
    padding: 5px;
    font-size: 24px;
    margin: 0;
  }
  .header-info .proj-type-count p {
    margin: 12px 10px 0 2px;
    font-size: 12px;
  }
}
hr.embossed {
  width: 100%;
  height: 0;
  color: none;
  background: none;
  border: none;
  border-bottom: 1px solid #e6e6e6;
  border-top: 1px solid #f0f0f0;
}
/* Step 3 */
.p-b-10 {
  padding-bottom: 15px;
}
.detail-list {
  margin-bottom: 20px;
}
.detail-list .facts-icon {
  font-size: 40px;
  width: 40px;
  float: left;
  margin-right: 10px;
  color: #5F5F5F;
}
.detail-list .unit-label {
  float: left;
}
.detail-list .unit-label h3 {
  margin: 0px 0px 6px;
}
.panel-title {
  border-bottom: 1px solid #ccc;
}
.panel-heading .accordion-toggle:after {
  /* symbol for "opening" panels */
  font-family: icomoon;
  /* essential for enabling glyphicon */
  content: "\e642";
  /* adjust as needed, taken from bootstrap.css */
  float: right;
  /* adjust as needed */
  color: grey;
  /* adjust as needed */
  transform: rotate(0deg);
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
}
.panel-heading .accordion-toggle.collapsed:after {
  /* symbol for "opening" panels */
  font-family: icomoon;
  /* essential for enabling glyphicon */
  content: "\e642";
  /* adjust as needed, taken from bootstrap.css */
  float: right;
  /* adjust as needed */
  color: grey;
  /* adjust as needed */
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
}
.property-sprite {
  background: url(../../images/sprite-popovergray.png) no-repeat;
  width: 40px;
  height: 40px;
  display: block;
}
.villa-ico {
  background: url(../../images/sprite-popovergray.png) no-repeat;
  width: 40px;
  height: 40px;
  display: block;
