/* SVG */
.svg-maps div.active {
  display: block;
}
.svg-maps div.inactive {
  display: none;
}
.detail {
  position: absolute;
  width: 100px;
  background: #ffffff;
  opacity: 0.7;
  padding: 10px;
  border: 1px dashed #000000;
  z-index: 1000;
}
.project-map {
  position: relative;
}
#spritespin,
.svg-maps div {
  position: absolute;
  top: 0px;
  left: 0px;
  transition: 0.3s all cubic-bezier(0.4, 0, 0.2, 1);
}
.zoom {
  animation: project-zoom 1s linear forwards;
  -moz-animation: project-zoom 1s linear forwards;
}
@keyframes project-zoom {
  from {
    transform: scale(1);
    opacity: 1;
  }
  to {
    transform: scale(3);
    opacity: 0;
  }
}
.text-primary {
  color: #f68121;
}
.tooltip-overlay {
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.5);
}
/* Loader */
.cf-loader {
  position: absolute;
  bottom: 30px;
  left: 0;
  right: 0;
  text-align: center;
  display: block;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -moz-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
